@use './sass/sections/greeting.scss';
@use './sass/sections/project.scss';
@use './sass/shared/colors' as c;
@use './sass/shared/elements';
@use './sass/sections/navbar';

body, html, :root {
  --background: #160721;
  --primary: #E0B071;
  --secondary: #000000;
  --accent: #71E0B0;
  --text: #F1E6F9;

  font-family: "Source Code Pro", monospace;
  font-size: 16px;
  margin: 0;
  padding: 20px;
  scroll-behavior: smooth;
}

.light-theme {
  background-color: #f0f0f0;
  --background: #f0f0f0;
  --primary: #b2b2b3;
  --secondary: #e3e3e3;
  --accent: #7e7d7c;
  --text: #0f0f0f;
}

.dark-theme {
  background-color: #160721;
  --background: #160721;
  --primary: #E0B071;
  --secondary: #000000;
  --accent: #71E0B0;
  --text: #F1E6F9;
}

.section {
  height: 91vh;
}

.section-gap {
  height: 9vh;
}
